
.list-item-skill {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 30px;
  font-size: 20px;
}
.list-item-skill input {
  border: none;
  font-size: 20px;
}
.list-item-skill input.title {
  width: 120px;
}
.list-item-skill input.percent {
  width: 30px;
  text-align: right;
}
.list-item-skill span.line {
  position: relative;
  display: inline-block;
  width: 520px;
  height: 8px;
  background-color: #c2c2c2;
}
.list-item-skill span.line .show {
  content: '';
  position: absolute;
  height: 100%;
}



