
.list-item {
  position: relative;
}
.list-item .list-control {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  border: 1px solid #dad8d7;
  border-radius: 4px;
  width: 160px;
  height: 30px;
  left: 50%;
  top: 50%;
  margin-top: -15px;
  margin-left: -80px;
  background-color: white;
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}
.list-item .list-control > span {
  width: 50%;
  height: 100%;
  text-align: center;
  line-height: 30px;
  cursor: pointer;
}
.list-item .list-control > span:hover {
  color: white;
  background-color: rgba(0, 0, 0, 0.9);
}
.list-item .list-control > span:first-child {
  border-radius: 4px 0 0 4px;
}
.list-item .list-control > span:last-child {
  border-radius: 0 4px 4px 0;
}



