
.context-list {
  width: 100%;
  margin-bottom: 30px;
}
.context-list .list-heading {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.context-list .list-heading .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.context-list .list-heading .title .img {
  margin: 0 20px 0 50px;
}
.context-list .list-heading:hover .add {
  display: block;
}
.context-list .list-heading .add {
  display: none;
}
.context-list .list-heading button {
  width: 30px;
  height: 30px;
  font-size: 24px;
  border-radius: 50%;
  background: none;
  border: 1px solid #dad8d7;
  cursor: pointer;
}
.context-list .list-heading button:hover {
  background: #f2f2f2;
}
.icon-class {
  background-color: #f6f7f7;
  border-bottom: 1px solid #e9e8e8;
}
.icon-margin-bottom {
  margin-bottom: 0;
}
.icon-margin-right {
  margin-right: 15px;
}



